.title-biggest {
    font-family: $corp;
    font-size: 48px;
    font-weight: 800;
    color: $title_dark;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    @include sm {
        font-size: 32px; } }

.title-big {
    font-family: $corp;
    font-size: 32px;
    font-weight: 800;
    letter-spacing: -0.02em;
    color: $title_dark;
    text-transform: uppercase;
    word-spacing: 0.1em;
    @include md {
        font-size: 28px; }
    @include sm {
        font-size: 26px; } }

.title-middle {
    font-family: $corp;
    font-size: 29px;
    font-weight: 700;
    letter-spacing: -0.03em;
    color: $text_black;
    word-spacing: 0.15em;
    @include md {
        font-size: 26px; }
    @include sm {
        font-size: 24px; } }

.title-small {
    font-family: $corp;
    line-height: 1;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: -0.05em;
    text-align: left;
    color: $text_black; }

.text {
    font-family: $corp;
    font-size: 18px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: $dark_blue;
    line-height: 1.22;
    &__link {
        text-decoration: underline;
        color: $text-link;
        font-weight: 700; }
    &--bold {
        color: $text_black;
        font-weight: bold; }
    p {
        margin-bottom: 16px; }
    //     &__single-review
    //     margin-bottom: 0.5em
    //     font-size: 17px
    //     line-height: normal
    //     color:
    // &__text
    //     font-weight: 300
    //     margin-bottom: 0.5em
    // &__details
 }    //     font-weight: bold
.subtitle {
    font-family: $corp;
    font-size: 20px;
    font-weight: 700;
    color: $text_black;
    @include md {
        font-size: 18px; }
    @include sm {
        font-size: 16px; } }

.text-big {
    font-size: 20px;
    font-family: $corp;
    line-height: 1.22;
    letter-spacing: -0.01em;
    p {
        margin-bottom: 16px; }
    &--italic {
        font-style: italic; }
    @include md {
        font-size: 18px; }
    @include sm {
        font-size: 16px; } }

.testimonial {
    margin-bottom: 42px;
    text-align: center;
    font-family: $corp;
    @include sm {
        margin-bottom: 24px;
        text-align: left; }
    &__text {
        font-style: italic;
        font-weight: 400;
        margin-bottom: 8px;
        @include sm {
            font-size: 14px;
            margin-bottom: 6px; } }
    &__details {
        font-weight: 700;
        @include sm {
            font-size: 14px; } } }
